@import url(https://fonts.googleapis.com/css2?family=KoHo&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'KoHo', sans-serif;
  font-weight: 100;
  height: 100vh;
  background: linear-gradient(
    to right,
    #e63946,
    #f1faee,
    #a8dadc,
    #457b9d,
    #1d3557
  );
}

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.App-title {
  margin-top: 1rem;
  color: #edf2f4;
  margin-bottom: 0;
  font-weight: 100;
  display: inline-block;
  font-size: 3rem;
  width: 50%;
}

.Die {
  color: white;
  text-shadow: 0 19px 38px rgba(0, 0, 0, 0.8), 0 15px 12px rgba(0, 0, 0, 0.7);
  transition: all 0.3s ease;
}

.Die-locked {
  opacity: 0.5;
  text-shadow: none;
}

.Die:not(.Die-locked):hover {
  cursor: pointer;
  opacity: 0.8;
}
.Die-rolling {
  animation: spin 1s ease-out;
}

.Die-rolling:hover,
.Die[disabled] {
  cursor: not-allowed;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.Dice {
  margin: 1em;
  display: flex;
  justify-content: space-between;
}

.Game {
  width: 30%;
  background: #edf2f4;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  margin: 10px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h2 {
  font-weight: 100;
  font-size: 1.5em;
  border-bottom: 2px solid purple;
  display: inline-block;
}

.Game-dice-section {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
}

.Game-button-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Game-reroll {
  font-size: 1em;
  color: #011627;
  font-weight: 100;
  transition: 0.5s;
  background-size: 200% auto;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(
    to right,
    #80ffdb,
    #56cfe1,
    #5390d9,
    #7400b8
  );
  border-radius: 0.5rem;
  border: none;
  width: 60%;
  padding: 5px;
  margin-bottom: 1rem;
}

.Game-reroll:hover {
  cursor: pointer;
  background-position: right center;
  color: #dfe7fd;
}

.Game-reroll:active,
.Game-reroll:focus {
  outline: none;
}

.Game-reroll:disabled {
  background-color: #ddd;
  cursor: not-allowed;
  opacity: 0.5;
}

.Game-description {
  font-style: italic;
  color: white;
}

.Game-header {
  width: 100%;
  background: linear-gradient(-45deg, #003566, #b7094c);
  background-size: 400% 400%;
  animation: Gradient 15s ease infinite;
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.RuleRow {
  transition: all 0.2s;
}
.RuleRow-active:hover {
  cursor: pointer;
  background: rgba(227, 242, 253, 0.5);
}

.RuleRow-disabled {
  background: linear-gradient(
    to right,
    rgba(209, 196, 233, 1) 0%,
    rgba(209, 196, 233, 1) 0%,
    rgba(209, 196, 233, 1) 30%,
    rgba(227, 242, 253, 1) 40%
  );
  background-size: 300% 300%;
  animation: Slide 1s ease 1;
}

@keyframes Slide {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 51%;
  }
}
.RuleRow-disabled td:first-child {
  text-decoration: line-through;
}

.RuleRow-disabled:hover {
  cursor: not-allowed;
}

.RuleRow td {
  border-bottom: solid 1px black;
  padding: 1px;
}

.RuleRow-name {
  width: 50%;
  text-align: left;
}

.RuleRow-score {
  width: 50%;
  text-align: right;
}

.ScoreTable {
  display: flex;
  flex-direction: column;
  font-size: 0.9rem;
  width: 95%;
  background-color: #edf2f4;
  padding: 5px;
}

.ScoreTable table {
  width: 100%;
  margin: 0 auto;
  min-width: 200px;
}

.ScoreTable-section {
  vertical-align: top;
  width: 100%;
}

