.Game {
  width: 30%;
  background: #edf2f4;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  margin: 10px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h2 {
  font-weight: 100;
  font-size: 1.5em;
  border-bottom: 2px solid purple;
  display: inline-block;
}

.Game-dice-section {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
}

.Game-button-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Game-reroll {
  font-size: 1em;
  color: #011627;
  font-weight: 100;
  transition: 0.5s;
  background-size: 200% auto;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(
    to right,
    #80ffdb,
    #56cfe1,
    #5390d9,
    #7400b8
  );
  border-radius: 0.5rem;
  border: none;
  width: 60%;
  padding: 5px;
  margin-bottom: 1rem;
}

.Game-reroll:hover {
  cursor: pointer;
  background-position: right center;
  color: #dfe7fd;
}

.Game-reroll:active,
.Game-reroll:focus {
  outline: none;
}

.Game-reroll:disabled {
  background-color: #ddd;
  cursor: not-allowed;
  opacity: 0.5;
}

.Game-description {
  font-style: italic;
  color: white;
}

.Game-header {
  width: 100%;
  background: linear-gradient(-45deg, #003566, #b7094c);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
  -moz-animation: Gradient 15s ease infinite;
  animation: Gradient 15s ease infinite;
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
